import React, { useState, useEffect } from 'react'

const Contactus = () => {

    const [formValues, setFormValues] = useState({
        username: "",
        mnumber: "",
    });
    const [formErrors, setFormErrors] = useState({});
    const [isSubmit, setIsSubmit] = useState(false);

    const inputChange = (e) => {
        const name = e.target.name;
        const value = e.target.value;
        setFormValues({ ...formValues, [name]: value });
    }
    const formSubmit = (e) => {
        e.preventDefault();
        setFormErrors(validateform(formValues));
        setIsSubmit(true);

    }

    useEffect(() => {
        if (Object.keys(formErrors).length === 0 && isSubmit) {
            console.log(formErrors);

        }
    }, [formErrors]);

    const validateform = (values) => {
        const errors = {};
        if (!values.username) {
            errors.username = "UserName is requried!";
        }
        if (!values.mnumber) {
            errors.mnumber = "Mobile Number is requried!";
        } else if (values.mnumber.length < 7) {
            errors.mnumber = "Mobile Number is not valid!";
        }
        return errors;
    }
    return (
        <>
            <div className="container-fluid contact-bg ">
                <div className="row">
                    <div className="col-12 mx-auto">
                        <div className="contact-title">
                            <h3>Contact Form</h3>
                        </div>
                        {Object.keys(formErrors).length === 0 && isSubmit ? (<div className='massage-sent'>Message Sent Successfully</div>) : (<></>)}
                        <form action="" onSubmit={formSubmit}>
                            <div className="input-field">
                                <div className="input-user">
                                    <input type="text" name="username" id="username" onChange={inputChange}
                                        value={formValues.username} placeholder='Name' autocomplete="off" />
                                </div>
                                <p>{formErrors.username}</p>
                                <div className="input-user">
                                    <input type="tel" name="mnumber" id="mnumber" onChange={inputChange}
                                        value={formValues.mnumber} placeholder='Number' autocomplete="off" />
                                </div>
                                <p>{formErrors.mnumber}</p>
                                <button className="btn btn-success btn-form" type="submit" >Contact Me</button>
                            </div>
                        </form>

                    </div>
                </div>
            </div>
        </>
    )
}

export default Contactus